import React from "react";

import "./GrowYourBusinessToday.css";

import { useNavigate } from "react-router-dom";

export default function GrowYourBusinessToday() {
  const handleMouseMove = (e) => {
    const maxX = window.innerWidth;
    const maxY = window.innerHeight;

    const el = document.querySelector("#grow-your-business-today");

    el.style.backgroundPositionX = (-e.clientX / maxX) * 100 + "px";
    console.log(el.style.backgroundPositionX);

    el.style.backgroundPositionY = (-e.clientY / maxY) * 100 + "px";
  };

  const navigate = useNavigate();

  return (
    <div
      className="grow-your-business-today-main-container"
      id="grow-your-business-today"
      onMouseMove={handleMouseMove}
      /* onMouseEnter={handleMouseMove} */
    >
      <div className="grow-your-business-today-main-container-1">
        <h1>Start Your Journey To Business Growth Today</h1>

        <button
          onClick={() => {
            navigate("/contactus");
          }}
        >
          Reach out
        </button>
      </div>
    </div>
  );
}
