import React, { useState, useEffect } from "react";

import "./Services_banner.css";

import { useNavigate } from "react-router-dom";

export default function Services_banner() {
  const navigate = useNavigate();

  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  return (
    <div>
      <div
        className={`services-page-banner slide-in ${
          isMounted ? "slide-in-active" : ""
        }`}
      >
        <div className="services-banner-content-1">
          <div>
            <h1>Website Design Agency</h1>

            <h3>
              We don’t just construct websites, we construct a website that
              boosts purchases
            </h3>

            <div className="services-banner-content-1-buttons">
              <button
                onClick={() => {
                  navigate("/contactus");
                }}
              >
                Let’s chat
              </button>

              <button
                onClick={() => {
                  navigate("/portfolio");
                }}
              >
                Explore our projects{" "}
              </button>
            </div>
          </div>
        </div>

        <div className="services-banner-content-2"></div>
      </div>
    </div>
  );
}
