import React from "react";
import "./ServicesDescription.css";

import { useNavigate } from "react-router-dom";

export default function ServicesDescription() {
  const navigate = useNavigate();

  return (
    <div className="services-decription-container">
      <br />
      <br />
      <br />

      <h1>
        Lead your users down a path of discovery by delivering an exceptional
        user experience with content designed for their enjoyment!
      </h1>

      <br />

      <p>
        The website design will facilitate users to quickly find what they are
        looking for, and simplify the user journey, Captivating visuals are used
        to engage users immediately upon arriving on the site, Our website
        design agency will design a user-friendly website to meet your
        objectives and needs, The intention is to maximize the results of these
        elements, designing with this aim will amplify the potential success. A
        successful website design can boost profitability and draw in customers.
        We will design website features attractive enough to drive revenue
        growth.
      </p>

      <br />

      <button
        onClick={() => {
          navigate("/contactus");
        }}
      >
        Let's Chat
      </button>
      <br />
      <br />
    </div>
  );
}
