import React, { useState, useRef } from "react";
import "./Website_design_and_development.css";

/* import { FaDesktop } from "react-icons/fa";
import { FaShoppingBasket } from "react-icons/fa"; */

import "intersection-observer";

import arrow_left from "./arrow-left.png";
import arrow_right from "./arrow-right-to-left.png";

import triangle1 from "./triangle1.png";
import triangle2 from "./triangle2.png";

import { Link } from "react-router-dom";

import pic1 from "./pic1.png";
import pic2 from "./pic2.png";

import CustomWhite from "./Custom-White.png";
import CustomPurple from "./Custome-Purple.png";

import WordPressWhite from "./Wordpress-White.png";
import WordPressPurple from "./Wordpress-Purple.png";

import SeoWhite from "./Seo-White.png";
import SeoPurple from "./Seo-Purple.png";

import EcommerceWhite from "./Ecommerce-White.png";
import EcommercePurple from "./Ecommerce-Purple.png";

export default function Website_design_and_development() {
  let cardOne = useRef();

  const [hoverCard1, setHoverCard1] = useState(false);

  const handleMouseEnterOne = () => {
    cardOne.current.style.backgroundColor = "#612d91";
    cardOne.current.style.color = "#fff";
    setHoverCard1(true);
  };

  const handleMouseLeaveOne = () => {
    cardOne.current.style.backgroundColor = "#fff";
    cardOne.current.style.color = "#000";
    setHoverCard1(false);
  };

  let cardTwo = useRef();

  const [hoverCard2, setHoverCard2] = useState(false);

  const handleMouseEnterTwo = () => {
    cardTwo.current.style.backgroundColor = "#612d91";
    cardTwo.current.style.color = "#fff";
    setHoverCard2(true);
  };

  const handleMouseLeaveTwo = () => {
    cardTwo.current.style.backgroundColor = "#fff";
    cardTwo.current.style.color = "#000";
    setHoverCard2(false);
  };

  let cardThree = useRef();

  const [hoverCard3, setHoverCard3] = useState(false);

  const handleMouseEnterThree = () => {
    cardThree.current.style.backgroundColor = "#612d91";
    cardThree.current.style.color = "#fff";
    setHoverCard3(true);
  };

  const handleMouseLeaveThree = () => {
    cardThree.current.style.backgroundColor = "#fff";
    cardThree.current.style.color = "#000";
    setHoverCard3(false);
  };

  let cardFour = useRef();

  const [hoverCard4, setHoverCard4] = useState(false);

  const handleMouseEnterFour = () => {
    cardFour.current.style.backgroundColor = "#612d91";
    cardFour.current.style.color = "#fff";
    setHoverCard4(true);
  };

  const handleMouseLeaveFour = () => {
    cardFour.current.style.backgroundColor = "#fff";
    cardFour.current.style.color = "#000";
    setHoverCard4(false);
  };

  return (
    <div className="design-and-development-main-container">
      <div className="design-and-development-main-container-row-1">
        <Link
          to="/services/customwebsitedesign"
          className="design-and-development-main-container-row-1-card-1"
          onMouseEnter={handleMouseEnterOne}
          onMouseLeave={handleMouseLeaveOne}
          ref={cardOne}
        >
          <img
            src={hoverCard1 ? CustomWhite : CustomPurple}
            className="design-and-development-main-container-row-1-card-1-icon"
            alt="card-1"
          />

          <br />

          <h1>Custom Website Design</h1>

          <br />

          <p>
            Websites are the main part of the digital world, it is a support
            system for your business, an efficient and responsive e-commerce
            website helps attract buyers and apprehend your brand’s products,
            and make your brand reach its business goal.
          </p>

          <img
            src={arrow_left}
            className="web-arrow-1"
            alt="an-arrow"
            onMouseEnter={handleMouseLeaveOne}
          />

          <img
            src={triangle1}
            className="web-triangle-1"
            alt="triangle 1"
            onMouseEnter={handleMouseLeaveOne}
          />
        </Link>

        <Link
          to="/services/wordpresswebsitedsign"
          className="design-and-development-main-container-row-1-card-2"
          onMouseEnter={handleMouseEnterTwo}
          onMouseLeave={handleMouseLeaveTwo}
          ref={cardTwo}
        >
          <img
            src={hoverCard2 ? WordPressWhite : WordPressPurple}
            className="design-and-development-main-container-row-1-card-1-icon"
            alt="card-2"
          />

          <br />

          <h1>WordPress Website Design</h1>

          <br />

          <p>
            We construct web designs in WordPress, creating high-end visuals and
            tailored functionality. Additionally, we fabricate innovative
            layouts specifically designed to meet clients' needs efficiently. We
            build dynamic pages using WordPress. Furthermore, our team
            manufactures sophisticated plugins for integration into these
            WordPress designs.
          </p>

          <img
            src={arrow_right}
            className="web-arrow-2"
            alt="an-arrow"
            onMouseEnter={handleMouseLeaveTwo}
          />

          <img
            src={triangle2}
            className="web-triangle-2"
            alt="triangle 2"
            onMouseEnter={handleMouseLeaveTwo}
          />
        </Link>

        <Link
          to="/services/seowebsite"
          className="design-and-development-main-container-row-1-card-3"
          onMouseEnter={handleMouseEnterThree}
          onMouseLeave={handleMouseLeaveThree}
          ref={cardThree}
        >
          <img
            src={hoverCard3 ? SeoWhite : SeoPurple}
            className="design-and-development-main-container-row-1-card-3-icon"
            onMouseEnter={handleMouseLeaveThree}
            alt="card-3"
          />

          <br />

          <h1>SEO website optimization</h1>

          <br />

          <p>
            At Interac8, we provide superior web design services and top-notch
            SEO optimization to enhance your website’s organic search traffic
            and attract more visitors to reach its full potential. We're a
            trusted website design agency that's committed to quality results!
          </p>

          <img
            src={arrow_left}
            className="web-arrow-1"
            alt="an-arrow"
            onMouseEnter={handleMouseLeaveThree}
          />

          <img
            src={triangle1}
            className="web-triangle-1"
            alt="triangle 1"
            onMouseEnter={handleMouseLeaveThree}
          />
        </Link>

        <Link
          to="/services/ecommercewebsite"
          className="design-and-development-main-container-row-1-card-4"
          onMouseEnter={handleMouseEnterFour}
          onMouseLeave={handleMouseLeaveFour}
          ref={cardFour}
        >
          <img
            src={hoverCard4 ? EcommerceWhite : EcommercePurple}
            className="design-and-development-main-container-row-1-card-4-icon"
            alt="card-4"
          />

          <br />

          <h1>E-commerce Website Design</h1>

          <br />

          <p>
            Our team of exceptionally talented professionals can help you build
            an unforgettable online shopping experience by providing custom
            website design solutions to help e-commerce businesses flourish, As
            a renowned e-commerce website design company in the United States,
            We offer a range of services that meet the needs and budgets of all
            kinds of companies.
          </p>

          <img
            src={triangle2}
            className="web-triangle-2"
            alt="triangle 2"
            onMouseEnter={handleMouseLeaveFour}
          />
        </Link>
      </div>

      <div className="design-and-development-main-container-row-2">
        <div className="design-and-development-main-container-row-2-pic1">
          <img src={pic1} alt="pic 20 here" style={{ width: "80%" }} />
        </div>

        <div className="design-and-development-main-container-row-2-pic2">
          <img src={pic2} alt="pic 20 here" style={{ width: "80%" }} />
        </div>
      </div>
    </div>
  );
}
