import React from "react";

import "./DesigningWebsites.css";

import designing_websites from "./pic1.png";

export default function DesigningWebsites() {
  return (
    <div className="designing-websites-container">
      <div className="designing-websites-container-1">
        <h1>
          Designing websites for the end user
        </h1>

        <p>
          An important aspect of website design is user experience. We make sure
          that the user's journey on a website or mobile app is smooth and
          engaging will help to maintain customer loyalty while achieving
          business objectives. The website design should facilitate positive
          experiences for users. It is important to optimize the web interface
          with user experience in mind. Designing a great web interface can
          enhance consumer satisfaction.
        </p>
      </div>

      <div className="designing-websites-container-2">
        <img
          src={designing_websites}
          style={{ width: "80%", height: "80%" }}
          alt="Designing websites here"
        />
      </div>
    </div>
  );
}
